import React, {useEffect} from 'react';
import styled from 'styled-components';
import ModalSlider from './ModalSlider';
import Stackicon from './Stackicon';


const ProjectModal = ({title, data, visible, onClose, img}) => {
    
    
    
    return (
    <>
        <Overlay visible={visible} onClick={onClose}>
            <Modal>
                <ModalTitle>{title}</ModalTitle>
                <ModalSlider img={img} />
                <ModalSection>
                    <Stacks>
                        {data.stacks.map(stack => (
                            <Stackicon data={stack}/>
                        ))}
                    </Stacks>
                    <ModalDetail>
                        {data.detail}
                    </ModalDetail>
                    <Links>
                        <Link href={data.links.git} >
                            <i className="fas fa-code"></i>
                        </Link>
                        <Link href={data.links.deploy}>
                            <i className="fas fa-external-link-alt"></i>
                        </Link>
                    </Links>
                </ModalSection>
                <Exit onClick={onClose}>✖</Exit>
            </Modal>
        </Overlay>
    </>
)};

export default ProjectModal;


const Overlay = styled.div`
    display: ${props => props.visible ? "flex" : "none"};
    background-color: rgba(255, 255, 255, 0.5);
    backdrop-filter: blur(2px);
    justify-content: center; align-items: center;
    position: fixed;
    top: 0; left: 0; bottom: 0; right: 0;
    z-index: 100;
`;
const Modal = styled.div`
    width: 700px; height: 80vh;
    background-color: white;
    position: relative;
    border-radius: 12px;
    box-shadow: 3px 2px 5px 1px rgba(0, 0, 0, 0.3);
`;
const ModalTitle = styled.h2`
    position: absolute;
    top: 20px; left: 50%; transform: translateX(-50%);
    z-index: 1;
    font-size: 2em; font-weight: 600;
`;
const ModalSection = styled.div`
    height: 50%;
    padding: 10px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
`;
const Stacks = styled.div`
    text-align: center;
    margin-bottom: 5px;
`
const ModalDetail = styled.div`
    flex-grow: 1;
    padding: 10px 20px;
`
const Links = styled.div`
    display: flex;
    align-items: center;
    justify-content: center;
`
const Link = styled.a`
    margin: 10px;
    i{
        font-size: 1.7em; color: black;
        transition: 0.1s;
        &:hover{
            transform: scale(1.08);
        }
    }
`
const Exit = styled.button`
    position: absolute;
    display: flex; justify-content: center; align-items: center;
    border: none; background-color: rgba(0, 0, 0, 0);
    font-size: 1em;
    width: 20px; height: 20px;
    right: 10px; top: 10px;
    transition: 0.2s;
    cursor: pointer;
    opacity: 0.5;
    z-index: 10;
    &:hover{
        transform: rotate(180deg) scale(1.3);
    }
`