import React, { useState } from 'react';
import styled from 'styled-components';
import ThankyouMessage from '../Components/ThankyouMessage';

import kakaostamp from "../assets/kakao_stamp.png";
import githubstamp from "../assets/git_stamp.png";

export default ({tab}) => {
    
    return (
        <Background ref={tab}>
            <Container>
                <Title>CONTACT</Title>
                <Subtitle>발전하는 신입개발자가 필요하시다면 아래 우편을 확인해주세요.</Subtitle>
                <Postcard>
                    <Form
                        className="gform"
                        method="POST"
                        action="https://script.google.com/macros/s/AKfycbzdHHW11UlA6Zllx6OQRgFjGMKBLlMIqpS1MGGcwXG3V4CrTGia1lndLMaG_NYjuilG/exec"
                    >
                        <FormEl>
                            <CardTextTitle>보내는 사람</CardTextTitle>
                            <Input type="text" placeholder="Enter Your Name" id="name" name="name" required></Input>
                            <Input type="mail" placeholder="Enter Your Email" id="email" name="email" required></Input>
                            <Textarea placeholder="Enter Your Message" id="message" name="message" rows="8" required></Textarea>
                            <Button>Send</Button>
                        </FormEl>
                        <ThankyouMessage />
                    </Form>
                    
                    <Stamps>
                        <Stamp href="https://github.com/JiyongInSpace" target="_blank">
                            <Img src={githubstamp}></Img>
                        </Stamp>
                        <Stamp href="https://open.kakao.com/o/sLed3HCd" target="_blank">
                            <Img src={kakaostamp}></Img>
                        </Stamp>
                    </Stamps>
                    <CardText>
                        <CardTextTitle>받는 사람</CardTextTitle>
                        <CardTextContent>
                            <span>신입 웹 프론트엔드 개발자 김지용</span>
                            <NumBox>
                                <Num>0</Num><Num>1</Num><Num>0</Num>-
                                <Num>3</Num><Num>5</Num><Num>4</Num><Num>3</Num>-
                                <Num>3</Num><Num>2</Num><Num>6</Num><Num>1</Num>
                            </NumBox>
                        </CardTextContent>
                    </CardText>
                </Postcard>
            </Container>
        </Background>
    );
}


const Background = styled.div`
    width: 100%;
    min-height: 1000px;
    display: flex;
    justify-content: center;
    align-items: center;
`;
const Container = styled.article`
    width: 1340px;
`;
const Title = styled.h2`
    font-size: 4em;
    text-align: center;
    font-weight: 800;
`;
const Subtitle = styled.p`
    text-align: center;
    color: gray;
    margin: 50px 0;
`
const Postcard = styled.section`
    width: 900px; height: 500px;
    margin: 0 auto;
    position: relative;
    box-shadow: 3px 2px 5px 1px rgba(0, 0, 0, 0.3);
    display: grid;
    grid-template-columns: 60% 40%;
    grid-template-rows: 30% 70%;
`;
const CardText = styled.div`
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    padding-bottom: 40px;
`;
const CardTextTitle = styled.h4`
    font-size: 1.5em;
    color: blue;
    font-weight: 500;
    padding: 20px 0 0 20px;
`;
const CardTextContent = styled.p`
    font-size: 1.2em;
    margin: 25px 0 10px 00px;
    padding: 0 0 0 20px;
`;
const NumBox = styled.div`
    display: flex;
    margin-top: 20px;
`;
const Num = styled.div`
    width:20px; height: 20px;
    border: 1px solid crimson;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-left: 2px;
`;
const Stamps = styled.div`
    margin-left: auto;
    display: flex;
    align-items: flex-start;
    padding: 25px;
`;
const Stamp = styled.a`
    display: block;
    width: 90px; height: 120px;
    background-color: white;
    transition: 0.3s;
    &:hover{
        transform: scale(1.05);
    }
`;
const Img = styled.img`
    width: 100%; height: 100%;
    cursor: pointer;
`;


const Form = styled.form`
    width: 100%; height: 100%;
    grid-column: 1 / 2; grid-row: 1 / 3;
`;
const FormEl = styled.div`
    width: 100%; height: 100%;
    display: flex;
    flex-direction: column;
    padding: 30px;
`;
const Input = styled.input`
    font-family: "Noto Sans KR", sans-serif;
    font-weight: 200;
    padding: 5px 20px;
    border: none;
    border-bottom: 1px solid lightgray;
    margin-top: 20px;
    font-size: 20px;
    &:focus{outline: none;}
`;
const Button = styled.button`
    font-family: "Noto Sans KR", sans-serif;
    font-weight: 200;
    padding: 5px 20px;
    border: none;
    border-bottom: 1px solid lightgray;
    margin-top: 20px;
    font-size: 20px;
    cursor: pointer;
    border-right: 1px solid lightgray;
    transition: 0.4s;
    &:active{transform: scale(0.99);}
    &:hover{background-color: gray; color:white;}

`;

const Textarea = styled.textarea`
    font-family: "Noto Sans KR", sans-serif;
    font-weight: 200;
    padding: 20px;
    margin-top: 10px;
    border: 1px solid lightgray;
    font-size: 20px;
    resize: none;
    &::-webkit-scrollbar{width:5px;}
    &::-webkit-scrollbar-thumb{background-color: white;}
    &::-webkit-scrollbar-track{background-color: silver;}
    &:focus{outline: none;}
`;